import React from 'react'
import { NavLink } from "react-router-dom";
import { AppBar, Box, Toolbar, Typography, Button, IconButton } from '@mui/material/';
import Icon from '../../images/favicon.png';
import './css/style.css';
const NavBar = () => {
    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static">
                    <Toolbar>
                        <IconButton sx={{ mr: 2 }} component={NavLink} to="/">
                            <img src={Icon} alt="favicon.ico" />
                        </IconButton>
                        <Typography variant="h6" component="div" sx={{ flexGrow: 1, fontWeight: 'bold', fontSize: 'h5.fontSize', fontFamily: 'Segoe UI Symbol' }} >
                            Music Player
                        </Typography>
                        <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
                            <Button component={NavLink} to="/" style={({ isActive }) => { return { color: isActive ? 'black' : 'white' } }}>Home</Button>
                            <Button component={NavLink} to="/about" style={({ isActive }) => { return { color: isActive ? 'black' : 'white' } }}>About</Button>
                        </Box>
                    </Toolbar>
                </AppBar>
            </Box>
        </div>
    )
}

export default NavBar
