import { Box, Typography, Button } from '@mui/material';
import { NavLink } from "react-router-dom";
import React from 'react';
import pic from '../../images/undraw_about_us_page_re_2jfm.svg';
const AboutPage = () => {
  return (
    <>
      <Box variant="div" textAlign='center' sx={{ backgroundImage: `url(${pic})`, backgroundRepeat: 'no-repeat', backgroundSize: 'cover', backgroundPosition: 'center', height: '87vh', width: '100%' }}>
        <Typography variant="h3" component="div" sx={{ mt: 2 }}>
          Welcome to Music Player
        </Typography>
        <Typography variant="h6" component="div" sx={{ mt: 2, mb: 2 }}>
          We are the team of talented developer making Music Player Web Application
        </Typography>
        <Button variant="outlined" component={NavLink} to="/contact">Contact Now</Button>
      </Box>
    </>
  )
}

export default AboutPage