import * as React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Slider from '@mui/material/Slider';
import IconButton from '@mui/material/IconButton';
import PauseRounded from '@mui/icons-material/PauseRounded';
import PlayArrowRounded from '@mui/icons-material/PlayArrowRounded';
import FastForwardRounded from '@mui/icons-material/FastForwardRounded';
import FastRewindRounded from '@mui/icons-material/FastRewindRounded';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import ppic from '../images/live-from-space.jpg';
import { useEffect } from 'react';
const Playsong = (props) => {

    useEffect(() => {
       setaudio(new Audio(props.settoplay[0].link));
       Setsongname(props.settoplay[0].name);
       Setsinger(props.settoplay[0].singer);
       setimg(props.settoplay[0].img);
       audio.pause();
    }, props.settoplay)
    
    const [audio, setaudio] = React.useState(new Audio(''));
    const [position, setPosition] = React.useState(0);
    const [paused, setPaused] = React.useState(false);
    const [audiotime, setaudiotime] = React.useState(0);
    const[songname, Setsongname] = React.useState('Non');
    const[singer, Setsinger]=React.useState('Non');
    const[img, setimg] = React.useState('Non');

    const setskiptpart  = (value) => {
        audio.currentTime = value;
    }

    if (paused) {
        audio.play();
        audio.addEventListener('timeupdate',() =>{
            setPosition(parseInt((audio.currentTime)));
            setaudiotime(parseInt(audio.duration));
        });
    } else {
        audio.pause();
    }

    function formatDuration(value) {
        const minute = Math.floor(value / 60);
        const secondLeft = value - minute * 60;
        return `${minute}:${secondLeft < 9 ? `0${secondLeft}` : secondLeft}`;
    }
    return (
        <Box sx={{ml: 5}}>
            <Card sx={{ maxWidth: 345, borderRadius: 5 }}>
                <Box sx={{ pl: 4, pr: 4, pt: 4 }} >
                    <CardMedia sx={{ borderRadius: '50%' }} component="img" height={250} image={img || ppic} />
                </Box>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div" textAlign='center'>{songname}</Typography>
                    <Typography variant="body2" color="text.secondary" textAlign='center'>{singer}</Typography>
                </CardContent>
                <Slider aria-label="time-indicator" size="large" value={position} min={0} step={1} max={audiotime} onChange={(_, value) => setskiptpart(value)} />
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mt: -1, }}>
                    <Typography sx={{ fontSize: '0.75rem', opacity: 0.38, fontWeight: 500, letterSpacing: 0.2, padding: '10px' }}>{formatDuration(audiotime)}</Typography>
                    <Typography sx={{ fontSize: '0.75rem', opacity: 0.38, fontWeight: 500, letterSpacing: 0.2, padding: '10px' }}>{formatDuration(position)}</Typography>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', mt: -1, }}>
                    <IconButton aria-label="previous song">
                        <FastRewindRounded fontSize="large" />
                    </IconButton>
                    <IconButton aria-label={paused ? 'play' : 'pause'}

                        onClick={() => setPaused(!paused)}>
                        {paused ? (
                            <>
                                <PauseRounded sx={{ fontSize: '3rem' }} />
                            </>
                        ) : (
                            <PlayArrowRounded sx={{ fontSize: '3rem' }} />
                        )}
                    </IconButton>
                    <IconButton aria-label="next song">
                        <FastForwardRounded fontSize="large" />
                    </IconButton>
                </Box>
            </Card>
        </Box>
    );
}

export default  Playsong