import React from 'react'
import { Grid, Typography } from '@mui/material';
import Playlist from '../PlayList';
import Playsong from '../Playsong';
import { useState } from 'react';
const HomePage = () => {
  const defaultdata =
    [
      { "id": 0, "name": "1 Hour Lofi.mp3", "singer": "saurabh", "link": "http://localhost/songs/1%20Hour%20Lofi.mp3", "img": "http://localhost/songs/images/undraw_back_to_school_inwc.svg" },
      { "id": 1, "name": "Lofi.mp3", "singer": "saurabh", "link": "http://localhost/songs/Lofi.mp3","img": "http://localhost/songs/images/undraw_balloons_re_8ymj.svg" },
      { "id": 2, "name": "Lofi1.mp3", "singer": "saurabh", "link": "http://localhost/songs/Lofi1.mp3","img": "http://localhost/songs/images/undraw_beer_celebration_re_0iqw.svg" },
      { "id": 3, "name": "Logo_Playlist", "singer": "saurabh", "link": "http://localhost/songs/Logo%20Playlist%20.mp3","img": "http://localhost/songs/images/undraw_fall_thyk.svg" },
      { "id": 4, "name": "coderelax", "singer": "saurabh", "link": "http://localhost/songs/coderelax.m4a","img": "http://localhost/songs/images/undraw_happy_music_g6wc.svg" },
      { "id": 5, "name": "Hart teaching","singer":"saurabh","link":"http://localhost/songs/Hart%20teaching.mp3","img":"http://localhost/songs/images/undraw_winners_re_wr1l.svg"},
      { "id": 6, "name": "Sufi Audio Jukebox 2017","singer":"saurabh","link":"http://localhost/songs/Sufi%20Audio%20Jukebox%202017.mp3","img":"http://localhost/songs/images/undraw_sunlight_tn-7-t.svg"},
      { "id": 7, "name": "Punjabi","singer":"saurabh","link":"http://localhost/songs/Punjabi.mp3","img":"http://localhost/songs/images/undraw_beer_celebration_re_0iqw.svg"}
    ]
  const [playsong, setsong] = useState('');
  const HandlePlay = e => {
    setsong(defaultdata[e]);
  }
  return (
    <>
      <Grid container sx={{ p: 5 }}>
        <Grid item lg={7} sm={4} md={4}>
          <Typography variant='h4'>
            MostPopular
          </Typography>
          <Playlist PlaylistData={defaultdata} sendData={HandlePlay} />
        </Grid>
        <Grid item lg={4} md={7} sm={7}>
          <Playsong settoplay={[playsong]} />
        </Grid>
      </Grid>
    </>
  )
}

export default HomePage
