import React,{useState} from 'react'
import { Paper, TableContainer, Table, TableCell, TableRow, TableBody } from '@mui/material';
import Checkbox from '@mui/material/Checkbox';
import FavoriteBorder from '@mui/icons-material/FavoriteBorder';
import Favorite from '@mui/icons-material/Favorite';
import LibraryMusicIcon from '@mui/icons-material/LibraryMusic';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import PauseCircleFilledIcon from '../images/PlayCircleOutlineIcon.gif';

const PlayList = (props) => {
const [show, setShow] = useState('');

  return (
    <>
      <Paper sx={{ m: 5 }}>
        <TableContainer>
          <Table>
            <TableBody>
              {
                props.PlaylistData.map(item => (
                  <TableRow  key={item.id}  onClick={() => {props.sendData(item.id); setShow(item.id)}}>
                    <TableCell>{item.id+1} </TableCell>
                    <TableCell>{item.name}</TableCell>
                    <TableCell>{<LibraryMusicIcon />}</TableCell>
                    <TableCell>{(show===item.id)?<img src={PauseCircleFilledIcon} width="30" height="30" alt='musicplayer'/>:<PlayCircleOutlineIcon />}</TableCell>
                    <TableCell>{<Checkbox icon={<FavoriteBorder />} checkedIcon={<Favorite />} />}</TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </>
  )
}

export default PlayList
